.wiki-page {
    margin: 20px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .wiki-page h1 {
    font-size: 2rem;
    margin-bottom: 20px;
  }
  
  .wiki-page p {
    line-height: 1.6;
    margin-bottom: 20px;
  }